import React ,{ useState } from 'react'
import Header from './Header';
import FAQ from './FAQ';





function App() {
   const [faqs, setfaqs] = useState([
      {
        question: 'Why park a domain name in Parkname?',
        answer: 'Parkname is the leading industry standard for domain name parking and monetization services. We offer a wide variety of services to help you achieve success.',
        open: false
      },
      {
        question: 'Why park a domain name in Parkname?',
        answer: 'Parkname is the leading industry standard for domain name parking and monetization services. We offer a wide variety of services to help you achieve success.',
        open: false
      },
      {
        question: 'Why park a domain name in Parkname?',
        answer: 'Parkname is the leading industry standard for domain name parking and monetization services. We offer a wide variety of services to help you achieve success.',
        open: false
      },
   ]);

   const toggleFAQ = index => {
     setfaqs(faqs.map((faq, i) => {
       if (i === index) {
        faq.open = !faq.open
       } else {
         faq.open = false;
       }
       

       return faq; 
     }))
   }

  return (
    <div className="App">
         <Header />
        <div className="faqs">
          {faqs.map((faq, i) => (
            <FAQ faq={faq} index={i} toggleFAQ={toggleFAQ} />
          ))}
        </div>      
      
    </div>
  );
}


export default App;
